<template>
    <v-ons-page id="landing">
        <div class="center">
            <h1>LOGIN</h1>
            <div  class="input">
                <label>メールアドレス</label><br>
                <input type="text" placeholder="example@yahoo.co.jp">
            </div>
            <div  class="input">
                <label>パスワード</label><br>
                <input type="password">
            </div>
            <div style="margin-bottom: 100px">
                <button class="login-btn" @click="login">ログイン</button>
            </div>
            <span class="link" @click="window.open('http://google.com', '_system')">アカウント作成</span>
        </div>
    </v-ons-page>
</template>

<script>
import PageMixin from './PageMixin'
import Page from './index'
export default {
    mixins: [ PageMixin ],
    methods: {
        login() {
            this.pushPage(Page.TodoList)
        }
    }
}
</script>

<style scoped>
.center {
    text-align: center;
    padding: 20px;
}

.input {
    margin: 0 auto;
    width: 260px;
    margin-bottom: 80px;
    text-align: left;
}
.input label {
    width: 100%;
    text-align: left;
}
.input input {
    border: none;
    width: 100%;
    border-bottom: solid 1px;
    font-size: 24px;
    text-align: center;
}

.login-btn {
    border: none;
    width: 260px;
    font-size: 18px;
    padding: 5px;
    color: #ffffff;
    background-color: #dc5236;
}

.link {
    color: #25a6d9;
    border-bottom: solid 1px #25a6d9;
}
</style>



