<template>
    <v-ons-page id="post-form">
        <h1 style="text-align: center">リア充っぽい投稿</h1>
        <form style="padding: 0 20px">
            <div class="row">
                <label for="todo">やったリア充っぽいこと</label><br>
                <v-ons-select v-model="selectTodo" id="todo">
                    <option v-for="todo in todos" :value="todo.id">
                        {{ todo.name }}
                    </option>
                </v-ons-select>
            </div>
            <div class="row" id="input-image">
                <label for="">リア充っぽい写真</label><br>
                <div class="display-image">
                    <img :src="imageSrc" alt="">
                </div>
                <label for="select-image" id="select-image-label">写真を選ぶ</label>
                <input type="file" id="select-image" @change="selectImage($event)">
            </div>
            <div>
                <button type="button" id="post-button" @click="post">投稿</button>
            </div>
        </form>
        <v-ons-modal :visible="postingWait">
            <p style="text-align: center">
                投稿中 <v-ons-icon icon="fa-spinner" spin></v-ons-icon>
            </p>
        </v-ons-modal>
    </v-ons-page>
</template>

<script>
import PageMixin from './PageMixin'
import Page from './index'
export default {
    mixins: [PageMixin],
    data() {
        return {
            selectTodo: {},
            todos: [
                {
                    "id": 1,
                    "name": "花火大会",
                    "base_point": "300"
                },
                {
                    "id": 2,
                    "name": "手持ち花火",
                    "base_point": "100"
                },
                {
                    "id": 3,
                    "name": "夏祭り",
                    "base_point": "300"
                },
                {
                    "id": 4,
                    "name": "虫取り",
                    "base_point": "50"
                },
                {
                    "id": 5,
                    "name": "キャンプ",
                    "base_point": "300"
                },
                {
                    "id": 6,
                    "name": "キャンプファイヤー",
                    "base_point": "700"
                },
                {
                    "id": 7,
                    "name": "BBQ",
                    "base_point": "100"
                },
                {
                    "id": 8,
                    "name": "肝試し",
                    "base_point": "100"
                },
                {
                    "id": 9,
                    "name": "プール",
                    "base_point": "50"
                },
                {
                    "id": 10,
                    "name": "ナイトプール",
                    "base_point": "1000"
                },
                {
                    "id": 11,
                    "name": "海水浴",
                    "base_point": "150"
                },
                {
                    "id": 12,
                    "name": "ビーチバレー",
                    "base_point": "100"
                },
                {
                    "id": 13,
                    "name": "サーフィン",
                    "base_point": "100"
                },
                {
                    "id": 14,
                    "name": "川遊び",
                    "base_point": "150"
                },
                {
                    "id": 15,
                    "name": "スイカ割り",
                    "base_point": "400"
                },
                {
                    "id": 16,
                    "name": "夏フェス",
                    "base_point": "300"
                },
                {
                    "id": 17,
                    "name": "流しそうめん",
                    "base_point": "100"
                },
                {
                    "id": 18,
                    "name": "天体観測",
                    "base_point": "100"
                }
            ],
            imageSrc: 'https://placehold.jp/cccccc/989898/240x200.png?text=No%20Image',
            postingWait: false,
            postResult: {
                "image_path": "https://real-juice.s3-ap-northeast-1.amazonaws.com/posts-1503450265.png",
                "point": "1000",
                "todo": {
                    "name": "ナイトプール",
                    "base_point": "1000"
                },
                "total_point": 1240,
                "getBonuses": [
                    {
                        "name": "異性と一緒ボーナス",
