                    }
                ],
                "getTitle": [
                    {
                        "name": "リア充への第一歩",
                        "point": "100"
                    }
                ]
            }
        }
    },
    methods: {
        selectImage($event) {
            let file = $event.target.files[0]
            let fileReader = new FileReader()
            fileReader.onload = evt => {
                this.imageSrc = evt.target.result
            }
            fileReader.readAsDataURL(file)
        },
        post() {
            this.postingWait = true
            setTimeout(() => {
                this.postingWait = false
            }, 5000)
        }
    }
}
</script>

<style scoped>
    .row {
        margin-bottom: 20px;
    }
    #todo {
        width: 100%;
    }
    #input-image img {
        width: 100%;
        height: auto;
    }
    .display-image {
        width: 100%;
        margin-bottom: 10px;
    }
    .display-image img {
        width: 100%;
        height: auto;
    }
    #select-image {
        display: none;
    }
    #select-image-label {
        display: inline-block;
        width: 100px;
        text-align: center;
        background-color: #f90;
        color: #fff;
        border-radius: 10px;
        padding: 8px;
    }
    #post-button {
        width: 100%;
        border: none;
        background-color: #4169E1;
        color: #fff;
        padding: 8px;
        border-radius: 10px;
        font-size: 24px;
    }
</style>



